<template>
  <div class="mt-10 mx-5" v-if="loaded && !error">
    <h2 class="text-h3 mb-3">Что случилось?</h2>

    <p>Как пишут на сайте WWF, в России осталось всего 600 тигров. И это реально проблема.</p>

    <p>Сохранение природы, о котором написано в конституции &mdash; это круто, но чтобы ее реально сохранить,
      нужно что-то делать, а не просто написать об этом в никому не нужной книжке.</p>

    <p>Собственно поэтому я решил собрать <b>{{ parseInt(progress.target).toLocaleString('ru') }}&#8381;</b> и
      пожертвовать их в WWF.</p>

    <div class="my-15">
      <Progress :target="parseInt(progress.target)" :amount="parseInt(progress.amount)"/>

      <h1 class="mt-15 text-center display-2">Сбор завершен</h1>
      <p class="text-center mt-5">Спасибо всем, кто принял участие в моей безумной затее. Надеюсь, мы действительно сделали полезное дело!</p>
    </div>

    <div class="mb-15">
      <v-row>
        <v-col cols="6">
          <v-img :src="proofs[0]"/>
        </v-col>
        <v-col cols="6">
          <v-img :src="proofs[1]"/>
        </v-col>
      </v-row>
    </div>

    <div class="mb-15">
      <v-carousel
          cycle
          height="400"
          hide-delimiter-background
          show-arrows-on-hover
      >
        <v-carousel-item
            v-for="(slide, i) in slides"
            :key="i"
            :src="slide"
        >
          <v-sheet
              height="100%"
              color="rgba(255, 255, 255, 0)"
          >
            <div class="d-flex fill-height justify-end align-end">
              <div class="text-caption pa-1 white">&copy;{{ copyrights[i] }}</div>
            </div>
          </v-sheet>
        </v-carousel-item>
      </v-carousel>
    </div>

    <div v-if="members.length" class="mb-15">
      <h2 class="text-h3 mb-3">Список участников</h2>

      <v-row>
        <v-col :key="member.name" v-for="member in members" cols="12" sm="3" class="text-center">
          <v-img :src="avatars[member.avatar]" width="80%" max-width="150" class="mx-auto"/>
          <p>{{ member.name }}</p>
        </v-col>
      </v-row>
    </div>
  </div>
  <div class="mt-10 mx-5" v-else-if="error">
    <div class="text-center">
      <p>
        <v-icon size="80">mdi-emoticon-sad-outline</v-icon>
      </p>
      Произошла ошибка при загрузке данных. Попробуйте обновить страницу.
    </div>

    <div class="my-3 d-flex justify-center">
      <v-btn x-large color="green" dark to="/">Обновить</v-btn>
    </div>
  </div>
  <div class="mt-10 mx-5" v-else>
    <div class="text-center">
      <v-progress-circular
          :size="120"
          :width="3"
          color="green"
          indeterminate
      ></v-progress-circular>

      <p class="my-5">Идет загрузка, подождите...</p>
    </div>
  </div>
</template>

<script>
import Progress from "../components/Progress";
import {fetchProgress} from "../api";

import image1 from '../assets/uploads/01.jpg';
import image2 from '../assets/uploads/03.jpg';
import image3 from '../assets/uploads/04.jpg';
import image4 from '../assets/uploads/06.jpg';

import avatar1 from '../assets/avatars/1.png';
import avatar2 from '../assets/avatars/2.png';
import avatar3 from '../assets/avatars/3.png';
import avatar4 from '../assets/avatars/4.png';
import avatar5 from '../assets/avatars/5.png';
import avatar6 from '../assets/avatars/6.png';
import avatar7 from '../assets/avatars/7.png';
import avatar8 from '../assets/avatars/8.png';

import proof1 from '../assets/proofs.jpg';
import proof2 from '../assets/proofs-1.jpg';

export default {
  name: "index",
  components: {Progress},
  data() {
    return {
      avatars: {
        1: avatar1,
        2: avatar2,
        3: avatar3,
        4: avatar4,
        5: avatar5,
        6: avatar6,
        7: avatar7,
        8: avatar8,
      },
      slides: [
          image1,
          image2,
          image3,
          image4
      ],
      proofs: [
          proof1,
          proof2
      ],
      copyrights: [
        'Flickr.com / Tambako The Jaguar',
        'Flickr.com / Tambako The Jaguar',
        'Flickr.com / Tambako The Jaguar',
        'Flickr.com / Pionites melanocephalus'
      ],
      loaded: true,
      error: false,
      members: [],
      progress: {
        amount: 0,
        target: 0,
      }
    }
  },
  mounted() {
    fetchProgress()
        .then(r => {
          this.progress.amount = r.amount;
          this.progress.target = r.target;
          this.members = r.members;

          this.loaded = true;
        })
        .catch(() => {
          this.error = true;
        })

    document.title = "Спасаем тигров вместе • mznx.ru"
  }
}
</script>

